<template>
  <div>
     <!-- 头部 -->
    <HeaderGuide title="我的">
    </HeaderGuide>
    <!-- 内容 -->
    <div id="mine">
      <div class="information">
        <div class="top" @click="login">
          <div class="left">
            <img class="touxiang" src="./images/touxiang.jpg" alt="">
            <div class="login">
              <span class="denlu">登录/注册</span>
              <span class="phone iconfont icon-shouji">暂无绑定手机号</span>
            </div>
          </div>
          <div class="right">
            <i class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></i>
          </div>
        </div>
        <div class="bottom">
          <div class="bottom-cell">
            <div><span class="number yue">0.00</span><span class="yuan">元</span></div>
            <span class="bottom-name">我的余额</span>
          </div>
          <div class="bottom-cell">
            <div><span class="number youhui">0</span><span class="yuan">元</span></div>
            <span class="bottom-name">我的优惠</span>
          </div>
          <div class="bottom-cell">
            <div><span class="number jifen">0</span><span class="yuan">元</span></div>
            <span class="bottom-name">我的积分</span>
          </div>
        </div>
      </div>
      <div class="list">
        <mt-cell title="我的订单">
          <span class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></span>
          <img slot="icon" src="./images/dingdan.png" width="24" height="24">
        </mt-cell>
        <mt-cell title="积分商城">
          <span class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></span>
          <img slot="icon" src="./images/jifen.png" width="24" height="24">
        </mt-cell>
        <mt-cell title="外卖会员卡">
          <span class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></span>
          <img slot="icon" src="./images/card.png" width="24" height="24">
        </mt-cell>
      </div>
      <div class="list">
        <mt-cell title="服务中心">
          <span class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></span>
          <img slot="icon" src="./images/fuwu.png" width="24" height="24">
        </mt-cell>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderGuide  from '../../components/HeaderGuide/HeaderGuide'
export default {
  name: 'Personal',
  components:{
      HeaderGuide
  },
  methods: {
    login(){
      this.$router.push("/login/pwLogin");
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl"
#mine
  padding-top unit(40/$rem,rem)
  font-size unit(18/$rem,rem)
  .information
    top-border-1px(#fff)
    .top
      padding unit(27/$rem,rem) unit(10/$rem,rem)
      display flex
      background $green
      justify-content space-between
      align-items center
      color #fff
      .left
        display flex
        align-items center
        .touxiang
          width unit(60/$rem,rem)
          height unit(60/$rem,rem)
          border-radius 50%
        .login
          display flex
          flex-direction column
          justify-content center
          margin-left unit(10/$rem,rem)
          .denlu
            font-weight bold
            margin-bottom unit(10/$rem,rem)
          .phone
            font-size unit(16/$rem,rem)
     .bottom
      background #fff
      display flex
      .bottom-cell
        width unit(125/$rem,rem)
        display flex
        flex-direction column
        justify-content center
        align-items center
        padding unit(15/$rem,rem) 0
        border-right 1px solid #ccc
        .bottom-cell:last-child
          border-right 0
        .number
          font-size unit(30/$rem,rem)
          font-weight bold
        .yue
          color orange 
        .youhui
          color red
        .jifen
          color green  
        .yuan
          font-size unit(14/$rem,rem)
        .bottom-name
           margin-top unit(10/$rem,rem) 
           font-size unit(16/$rem,rem)  
  .list
    margin-top unit(14/$rem,rem)         
</style>



