<template>
  <div>
    <!-- 头部 -->
    <HeaderGuide :title="title">
      <span slot="left" class="header-left iconfont icon-sousu" @click="search_place"></span>
      <span slot="right" @click="login">登录|注册</span>
    </HeaderGuide>
    <!-- 内容 -->
    <div id="home">
      <!-- 类型介绍 -->
      <mt-swipe class="swipe" :auto="0">
          <mt-swipe-item>
            <div class="cell-list">
              <div class="cell" v-for="(items,index) in type_list" :key="index">
                <img :src="items.imgsrc" alt="">
                <span>{{items.title}}</span>
              </div>
            </div>
          </mt-swipe-item>
          <mt-swipe-item>
            <div class="cell-list">
              <div class="cell" v-for="(items,index) in type_list2" :key="index">
                <img :src="items.imgsrc" alt="">
                <span>{{items.title}}</span>
              </div>
            </div>
          </mt-swipe-item>
      </mt-swipe>
      <!-- 附近商家 -->
      <div class="shop-list">
        <div class="shangjia-div">
          <span class="shangjia iconfont icon-zhedie"> 附近商家</span>
        </div>
        <div class="shop" v-for="(items,index) in shop_list" :key="index" @click="goods_detail">
          <img class="shop-img" :src="items.imgsrc" alt="">
          <div class="shop-content">
            <div class="shop-title"><span class="pinpai" v-show="items.pinpai">品牌</span><span class="title">{{items.title}}</span></div>
            <div class="shop-danshu">
              <div>
                <span>
                  <i class="star iconfont icon-icon-" v-for="(items,index) in items.starNumber" :key="index"></i>
                </span>
                <span class="star-number">{{items.starNumber}}.0</span><span class="danshu">月售{{items.danshu}}单</span>
              </div>
              <div class="time">{{items.time}}分钟|{{items.luchen}}km</div>
            </div>
            <div class="shop-qisong"><span>￥{{items.qisong}}起送 / 配送费约￥{{items.peisong}}</span><div class="zhuanmai" v-show="items.zhuansong">美团专送</div></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import API from '../../api/api.js'
import HeaderGuide  from '../../components/HeaderGuide/HeaderGuide'
export default {
  name: 'Home',
  components:{
      HeaderGuide
  },
  beforeCreate(){
    document.body.parentNode.style.overflowY = "auto";
  },
  data() {
    return {
      title:'东莞市厚街镇尚美路鱼粥道',
      type_list:[
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料'}
      ],
      type_list2:[
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'},
        {imgsrc:require('./images/tianpin.jpg'),title:'甜品饮料2'}
      ],
       shop_list:[
        {imgsrc:require('./images/tianpin.jpg'),title:'好吃的店',pinpai:true,starNumber:5,danshu:'126',time:'30',luchen:'1.7',qisong:'20',peisong:'5',zhuansong:true},
        {imgsrc:require('./images/tianpin.jpg'),title:'美丽的面包店',pinpai:true,starNumber:3,danshu:'80',time:'20',luchen:'2.0',qisong:'15',peisong:'3',zhuansong:false},
        {imgsrc:require('./images/tianpin.jpg'),title:'烧烤店',pinpai:false,starNumber:4,danshu:'50',time:'40',luchen:'8.3',qisong:'50',peisong:'10',zhuansong:true},
        {imgsrc:require('./images/tianpin.jpg'),title:'沙县小吃',pinpai:false,starNumber:2,danshu:'100',time:'10',luchen:'4.0',qisong:'17',peisong:'5',zhuansong:false}
      ]
    }
  },
  methods: {
    login(){
      this.$router.push("/login");
    },
    search_place(){
      Toast('地方查询功能待完成');
    },
    goods_detail(){
       this.$router.push("/GoodsDetail");
    }
  },
  async mounted() {
    // const a= await API.test({})
    // console.log(a)
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl";
.header-left
  display flex
  font-size unit(20/$rem,rem)
#home
  overflow-y: auto
  padding-top unit(40/$rem,rem)
  .swipe
    height unit(230/$rem,rem)
    bottom-border-1px(rgba(0,0,0,0.3))
    background #fff
    .mint-swipe-indicators
      .mint-swipe-indicator.is-active
        background $green
        opacity 1
    .cell-list
      display flex
      flex-wrap wrap
    .cell
      width unit(93.75/$rem,rem)
      padding-top unit(8/$rem,rem)
      padding-bottom unit(20/$rem,rem)
      display flex
      flex-direction column
      justify-content center
      align-items center
      img
        width unit(60/$rem,rem)
        height unit(60/$rem,rem)
        margin-bottom unit(10/$rem,rem)
        border-radius 50%
      span
        display block
        font-size unit(15/$rem,rem)
        text-align center
  .shop-list
    background #fff
    margin-top unit(15/$rem,rem)
    top-border-1px(#000000)
    padding 0 unit(10/$rem,rem)
    .shangjia-div
      padding-top unit(8/$rem,rem)
      margin-bottom unit(18/$rem,rem)
      .shangjia
        font-size unit(18/$rem,rem)
        color rgba(0,0,0 0.5)
        font-weight bold
    .shop
      display flex
      align-items center
      margin-bottom unit(30/$rem,rem)
    .shop:last-child
       margin-bottom  0
       padding-bottom unit(5/$rem,rem)
      .shop-img
         width unit(80/$rem,rem)
         height  unit(80/$rem,rem)
      .shop-content
        padding-left unit(10/$rem,rem)
        width  unit(275/$rem,rem)
        .shop-title
          display flex
          align-items  center
          .pinpai
            font-size unit(14/$rem,rem)
            font-weight bold
            background orange
            margin-right unit(5/$rem,rem)
            padding unit(2/$rem,rem)
          .title
            font-size unit(20/$rem,rem)
            font-weight bold
            width unit(160/$rem,rem)
            overflow hidden
            text-overflow ellipsis
            white-space nowrap
        .shop-danshu
          font-size unit(15/$rem,rem)
          margin-top  unit(16/$rem,rem)
          margin-bottom  unit(10/$rem,rem)
          display flex
          justify-content space-between
          align-items center
          .star
            font-size unit(16/$rem,rem)!important
            margin-right unit(-5/$rem,rem)
            color orange
          .star-number
            color orange
            margin-left unit(4/$rem,rem)
            margin-right unit(6/$rem,rem)
            font-size unit(16/$rem,rem) 
          .danshu
            font-size unit(13/$rem,rem)
          .time
            font-size unit(13/$rem,rem)
        .shop-qisong
          font-size unit(15/$rem,rem)
          display flex
          justify-content space-between  
          .zhuanmai
            background orange
            font-size unit(13/$rem,rem)
            padding unit(3/$rem,rem) unit(8/$rem,rem)
            border-radius unit(10/$rem,rem) 0 unit(10/$rem,rem)  0
</style>



