<template>
  <div>
    <!-- 头部 -->
    <GoodsDetailHeader></GoodsDetailHeader>
    <!-- 内容 -->
    <div class="tap">
      <router-link to="/goodsDetail/order" tag="span" replace>点餐</router-link>
      <router-link to="/goodsDetail/appraise" tag="span" replace>评价</router-link>
      <router-link to="/goodsDetail/business" tag="span" replace>商家</router-link>
    </div>
    <router-view></router-view>
    <GoodsDetailFooter v-show="!$route.meta.isGoodsFootHide"></GoodsDetailFooter>
  </div>
</template>

<script>
import GoodsDetailHeader  from '../../components/GoodsDetailHeader/GoodsDetailHeader'
import GoodsDetailFooter  from '../../components/GoodsDetailFooter/GoodsDetailFooter'
export default {
  name: 'GoodsDetail',
  components:{
    GoodsDetailHeader,
    GoodsDetailFooter
  },
   beforeCreate(){
    document.body.parentNode.style.overflowY = "hidden";
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl"
.tap
  display flex
  justify-content space-around
  font-size unit(18/$rem,rem)
  font-weight bold
  color #bcc0bc
  background #fff
  padding-top unit(15/$rem,rem)
  span
    padding-bottom unit(9/$rem,rem)
    padding-left unit(4/$rem,rem)
    padding-right unit(4/$rem,rem)
  .router-link-active
    color $green
    font-weight bold
    border-bottom unit(3/$rem,rem) solid $green
</style>



