import Vue from 'vue'
import Router from 'vue-router'
import Login from '../pages/login/Login.vue'
import DxLogin from '../pages/login/childVue/dxLogin/DxLogin.vue'
import PwLogin from '../pages/login/childVue/pwLogin/PwLogin.vue'
import Home from '../pages/home/Home.vue'
import OrderList from '../pages/orderList/OrderList.vue'
import Personal from '../pages/personal/Personal.vue'
import Search from '../pages/search/Search.vue'
import GoodsDetail from '../pages/goodsDetail/GoodsDetail.vue'
import Order from '../pages/goodsDetail/childVue/order/Order.vue'
import Appraise from '../pages/goodsDetail/childVue/appraise/Appraise.vue'
import Business from '../pages/goodsDetail/childVue/business/Business.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      redirect: '/login/pwLogin',
      meta: {
        isFootHide: true
      },
      children: [
        {
          path: '/login/dxLogin',
          meta: {
            isFootHide: true
          },
          component: DxLogin
        },
        {
          path: '/login/pwLogin',
          meta: {
            isFootHide: true
          },
          component: PwLogin
        }
      ],
      component: Login
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/goodsDetail',
      redirect: '/goodsDetail/order',
      children: [
        {
          path: '/goodsDetail/order',
          meta: {
            isFootHide: true
          },
          component: Order
        },
        {
          path: '/goodsDetail/appraise',
          meta: {
            isFootHide: true,
            isGoodsFootHide: true
          },
          component: Appraise
        },
        {
          path: '/goodsDetail/business',
          meta: {
            isFootHide: true,
            isGoodsFootHide: true
          },
          component: Business
        }
      ],
      component: GoodsDetail,
      meta: {
        isFootHide: true
      }
    },
    {
      path: '/orderList',
      component: OrderList
    },
    {
      path: '/personal',
      component: Personal
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/',
      redirect: '/home'
    }
  ]
})



