// api.js
import request from './request'
const api = {
  // 接口1
  test (data) {
    return request('/telematics/v3/weather?location=嘉兴&output=json&ak=5slgyqGDENN7Sy7pw29IUvrZ', data, 'get')
  },
  // 接口2
  logout (data) {
    return request('user/logout', data)
  }
}
export default api



