<template>
  <div>
    <!-- 头部 -->
    <HeaderGuide title="搜索">
    </HeaderGuide>
    <!-- 内容 -->
    <div id="search">
      <form action="" v-on:submit.prevent="">
        <mt-search
        v-model="searchValue"
        cancel-text="取消"
        placeholder="请输入商家名称(比日：“烧烤” 或者 “沙县”)">
        <div class="shop" v-for="(items,index) in shop_list" :key="index" @click="goods_detail">
          <img class="shop-img" :src="items.imgsrc" alt="">
          <div class="shop-content">
            <div class="shop-title"><span class="pinpai" v-show="items.pinpai">品牌</span><span class="title">{{items.title}}</span></div>
            <div class="shop-danshu">
              <div>
                <span>
                  <i class="star iconfont icon-icon-" v-for="(items,index) in items.starNumber" :key="index"></i>
                </span>
                <span class="star-number">{{items.starNumber}}.0</span><span class="danshu">月售{{items.danshu}}单</span>
              </div>
              <div class="time">{{items.time}}分钟|{{items.luchen}}km</div>
            </div>
            <div class="shop-qisong"><span>￥{{items.qisong}}起送 / 配送费约￥{{items.peisong}}</span><div class="zhuanmai" v-show="items.zhuansong">美团专送</div></div>
          </div>
        </div>
        </mt-search>
      </form>

    </div>
  </div>
</template>

<script>
import HeaderGuide  from '../../components/HeaderGuide/HeaderGuide'
export default {
  name: 'Search',
  data() {
    return {
      shop_list:[
       
        ],
      searchValue:""
    }
  },
  beforeCreate(){
    document.body.parentNode.style.overflowY = "auto";
    document.documentElement.scrollTop =0;
  },
  watch:{
    searchValue:function(newvs,oldvs){
       document.documentElement.scrollTop =0;
      if(newvs.indexOf("沙县")!=-1){
        this.shop_list=[
         {imgsrc:require('./images/tianpin.jpg'),title:'沙县1号店',pinpai:true,starNumber:5,danshu:'126',time:'30',luchen:'1.7',qisong:'20',peisong:'5',zhuansong:true},
         {imgsrc:require('./images/tianpin.jpg'),title:'沙县2号店',pinpai:true,starNumber:3,danshu:'80',time:'20',luchen:'2.0',qisong:'15',peisong:'3',zhuansong:false},
         {imgsrc:require('./images/tianpin.jpg'),title:'沙县3号店',pinpai:false,starNumber:4,danshu:'50',time:'40',luchen:'8.3',qisong:'50',peisong:'10',zhuansong:true},
         {imgsrc:require('./images/tianpin.jpg'),title:'沙县4号店',pinpai:false,starNumber:2,danshu:'100',time:'10',luchen:'4.0',qisong:'17',peisong:'5',zhuansong:false}
      ]
      }
      else if(newvs.indexOf("烧烤")!=-1){
        this.shop_list=[
         {imgsrc:require('./images/tianpin.jpg'),title:'韩式烧烤店',pinpai:true,starNumber:5,danshu:'126',time:'30',luchen:'1.7',qisong:'20',peisong:'5',zhuansong:true},
         {imgsrc:require('./images/tianpin.jpg'),title:'日式烧烤店',pinpai:true,starNumber:3,danshu:'80',time:'20',luchen:'2.0',qisong:'15',peisong:'3',zhuansong:false},
         {imgsrc:require('./images/tianpin.jpg'),title:'中式烧烤店',pinpai:false,starNumber:4,danshu:'50',time:'40',luchen:'8.3',qisong:'50',peisong:'10',zhuansong:true}
      ]
      }
      else{
        this.result_list=[]
      }
    }
  },
  components:{
    HeaderGuide
  },
  methods: {
     goods_detail(){
       this.$router.push("/GoodsDetail");
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl";
#search
  padding-top unit(40/$rem,rem)
  font-size unit(18/$rem,rem)
  color $green
  .mint-searchbar
    position fixed
    width 100%
    left 0
    top unit(40/$rem,rem)
  .mint-searchbar-cancel
    color $green
  .mint-search-list-warp
    padding-top unit(40/$rem,rem)!important
    background #fff
  .shop
      display flex
      align-items center
      margin-bottom unit(30/$rem,rem)
      color #000
      padding-top unit(8/$rem,rem)
    .shop:last-child
       margin-bottom  0
       padding-bottom unit(5/$rem,rem)
      .shop-img
         width unit(80/$rem,rem)
         height  unit(80/$rem,rem)
      .shop-content
        padding-left unit(10/$rem,rem)
        width  unit(275/$rem,rem)
        .shop-title
          display flex
          align-items  center
          .pinpai
            font-size unit(14/$rem,rem)
            font-weight bold
            background orange
            margin-right unit(5/$rem,rem)
            padding unit(2/$rem,rem)
          .title
            font-size unit(20/$rem,rem)
            font-weight bold
            width unit(160/$rem,rem)
            overflow hidden
            text-overflow ellipsis
            white-space nowrap
        .shop-danshu
          font-size unit(15/$rem,rem)
          margin-top  unit(16/$rem,rem)
          margin-bottom  unit(10/$rem,rem)
          display flex
          justify-content space-between
          align-items center
          .star
            font-size unit(16/$rem,rem)!important
            margin-right unit(-5/$rem,rem)
            color orange
          .star-number
            color orange
            margin-left unit(4/$rem,rem)
            margin-right unit(6/$rem,rem)
            font-size unit(16/$rem,rem) 
          .danshu
            font-size unit(13/$rem,rem)
          .time
            font-size unit(13/$rem,rem)
        .shop-qisong
          font-size unit(15/$rem,rem)
          display flex
          justify-content space-between  
          .zhuanmai
            background orange
            font-size unit(13/$rem,rem)
            padding unit(3/$rem,rem) unit(8/$rem,rem)
            border-radius unit(10/$rem,rem) 0 unit(10/$rem,rem)  0  
</style>



