<template>
  <div id="pwLogin">
    <mt-field  placeholder="手机/邮箱/用户名" v-model="name"></mt-field>
    <mt-field  placeholder="密码" :type="pwType" v-model="passwd"><mt-switch v-model="changePW"></mt-switch></mt-field>
    <mt-field  placeholder="验证码" v-model="validCode">
      <validCode value.sync=""></validCode>
    </mt-field>
    <mt-button class="login-btn" type="primary" @click="login">登录</mt-button>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import validCode  from '../../../../components/validCode/validCode'
export default {
  name: 'PwLogin',
  data() {
    return {
      name:'',
      passwd:'',
      validCode:'',
      changePW:false
    }
  },
  components:{
    validCode
  },
  computed: {
    pwType(){
      if(this.changePW){
       return 'text'
      }
      else{
       return  'password'
      }
    }
  },
  methods: {
    login(){
      const {name,passwd,validCode}=this
      if(!name){
        MessageBox('提示', '手机/邮箱/用户名不能为空');
      }
      else if(!passwd){
        MessageBox('提示', '密码不能为空');
      }
      else if(!validCode){
        MessageBox('提示', '验证码不能为空');
      }
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../../../common/stylus/mixins.styl"
#pwLogin
  .login-btn
    width 100%
    margin-top unit(30/$rem,rem)
    background #0bc98e
</style>



