<template>
  <div id="header">
    <span class="left"><slot name="left"></slot></span>
    <span class="title">{{title}}</span>
    <span class="right"><slot name="right"></slot></span>
  </div>
</template>

<script>
export default {
  name: 'HeaderGuide',
  props:{
    "title": {
      type: String
    }
  }
}
</script>


<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl";
#header
  height unit(40/$rem,rem)
  background $green
  display flex
  justify-content space-between
  align-items center
  padding 0 unit(10/$rem,rem)
  color #fff
  position fixed
  left 0
  top 0
  width 100%
  z-index 2
  .right
    display flex
    font-size unit(14/$rem,rem)
  .title
    font-size unit(18/$rem,rem)
    width unit(180/$rem,rem)
    overflow hidden
    text-overflow ellipsis
    white-space nowrap
    text-align center
</style>



