<template>
  <div id="login">
    <div class="jiantou iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-1" @click="goblack"></div>
    <div class="login-content">
      <h1 class="title">毫子外卖</h1>
      <div class="login-way">
        <router-link to="/login/dxLogin" tag="span" class="dxlogin login-ways"><span>短信登录</span></router-link>
        <router-link to="/login/pwLogin" tag="span" class="login-ways"><span>密码登录</span></router-link>
      </div>
      <router-view class="login-way-content"></router-view>
      <span class="about">关于我们</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  beforeCreate(){
    document.body.parentNode.style.overflowY = "hidden";
  },
  methods: {
    goblack(){
      this.$router.replace("/home")
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl"
#login
  height unit(667/$rem,rem)!important
  background #fff
  .jiantou
    font-size unit(25/$rem,rem)
    padding-top unit(5/$rem,rem)
  .login-content
    display flex
    flex-direction column
    justify-content center
    align-items center
    margin-top unit(60/$rem,rem)
    .title
      font-size unit(40/$rem,rem)
      color $green
    .login-way
      margin-top  unit(50/$rem,rem)
      margin-bottom  unit(20/$rem,rem)
      font-size unit(16/$rem,rem)
      .dxlogin
        margin-right  unit(25/$rem,rem)
      .login-ways
        padding-bottom unit(4/$rem,rem)
      .router-link-active
        color $green
        font-weight bold
        border-bottom 2px solid $green
    .login-way-content
      width unit(300/$rem,rem)
    .about
      font-size unit(14/$rem,rem)
      color #8c8989
      font-weight bold
      margin-top unit(30/$rem,rem)
</style>



