<template>
   <div id="dxLogin">
    <mt-field v-model="phone"  placeholder="手机号"><span v-show="!isTrue" class="yzm">{{setYZM}}</span><span v-show="isTrue" @click="getYZM">获取验证码</span></mt-field>
    <mt-field  placeholder="验证码"></mt-field>
    <div class="tips">温馨提示：未注册毫子外卖账号的手机号，登录时将自动注册，且代表已同意<span class="xieyi">《用户服务协议》</span></div>
    <mt-button class="login-btn" type="primary">登录</mt-button>
  </div>
</template>

<script>
export default {
  name: 'dxLogin',
  data() {
    return {
      isTrue:false,
      phone:'',
      setYZM:'获取验证码'
    }
  },
  watch: {
    phone(newvs){
      if(/^1[3456789]\d{9}$/.test(newvs)){
        this.isTrue=true
      }
      else{
        this.isTrue=false
      }
    }
  },
  methods: {
    getYZM(){
      let time=30
     const interval=setInterval(()=>{
        this.isTrue=false
        var newTime=time--;
        this.setYZM='已发送('+newTime+'s)'
        if(newTime<0){
          clearInterval(interval)
          this.setYZM="获取验证码"
          this.isTrue=true
        }
      },1000)
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../../../common/stylus/mixins.styl"
#dxLogin
  .yzm
    color #cccccc
  .login-btn
    width 100%
    margin-top unit(30/$rem,rem)
    background #0bc98e
  .tips
    font-size unit(13/$rem,rem)
    padding 0 unit(10/$rem,rem)
    color #8c8989
    line-height unit(20/$rem,rem)
    .xieyi
      color $green
      font-size unit(13.5/$rem,rem)
      font-weight bold
</style>



