<template>
  <div id="goodsDetailHeader">
    <div class="header" :style="havaHeaderBgImg?`background:url(${imgUrl})`:'background:green'"><i class="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-1" @click="$router.back()"></i></div>
    <div class="shop-ico" ><img class="shop-img" src="./images/tianpin.jpg" alt=""></div>
    <div class="shop-content">
      <div class="shop-title"><span class="pinpai">品牌</span><span class="title">美味鱼粥道(厚街店)</span><div class="xsj-rigth"></div></div>
      <div class="shop-desc"><span>3.5</span><span>月售90单</span><span>美团专送 约28分钟</span><span>距离 1km</span></div>
      <div class="shop-youhui" @click="youhui_popup"><div><span class="pinpai">首单</span><span class="title">{{youhui_select}}</span></div><div class="youhui-num"><span>8个优惠</span><div class="xsj-bottom"></div></div></div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom" class="popup">
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>

<script>
export default {
  name: 'GoodsDetailHeader',
  data() {
    return {
      imgUrl:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1575962607940&di=03ca1415f1632f7fa993347fc703c526&imgtype=0&src=http%3A%2F%2Fimage.f600.cn%2Fjmxiangmu%2F149E58C3F5A.jpg',
      popupVisible:false,
      youhui_select:'新用户下单立减17元',
      slots: [
        {
          flex: 1,
          values: ['优惠1', '优惠2', '优惠3', '优惠3', '优惠4', '优惠5', '优惠6', '优惠7', '优惠8'],
          className: 'slot1',
          textAlign: 'center'
        }
      ]
    }
  },
  methods: {
    onValuesChange(picker, values) {
      this.youhui_select=values[0]
    },
    youhui_popup(){
      this.popupVisible=true
    },
    validateImage(url)
    {    
        var ImgObj = new Image(); //判断图片是否存在  
      ImgObj.src = url;  
      //没有图片，则返回-1  
      if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {  
          return true;  
      } else {  
          return false;
      }  
      }
  },
  computed: {
    havaHeaderBgImg(){
      return this.validateImage(this.imgUrl)
    }
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl"
#goodsDetailHeader
  background #fff
  position relative
  .popup
    width 100%
  .header
    height unit(50/$rem,rem)
    background-position-y unit(200/$rem,rem)
    color #fff
    display flex
    align-items center
    i
      font-size unit(20/$rem,rem)
  .shop-ico
    width unit(70/$rem,rem)
    height  unit(70/$rem,rem)
    background $green
    position absolute 
    left unit(152.5/$rem,rem)
    top unit(10/$rem,rem)
    border #cccccc 1px solid 
    .shop-img
      width 100%
      height 100%
  .shop-content
    margin-top unit(40/$rem,rem)
    display flex
    justify-content center
    flex-direction column
    align-items center
    .shop-title
      display flex
      align-items  center
      justify-content center
      .pinpai
        font-size unit(14/$rem,rem)
        font-weight bold
        background orange
        margin-right unit(5/$rem,rem)
        padding unit(2/$rem,rem)
      .title
        font-size unit(23/$rem,rem)
        font-weight bold
        width unit(230/$rem,rem)
        overflow hidden
        text-overflow ellipsis
        white-space nowrap
      .xsj-rigth
        border-left unit(6/$rem,rem) solid #000
        border-top unit(6/$rem,rem) solid transparent
        border-bottom  unit(6/$rem,rem) solid transparent
        border-right unit(6/$rem,rem) solid transparent
        height 0
        width 0
    .shop-desc
      font-size unit(14/$rem,rem)
      text-align center
      margin unit(5/$rem,rem) 0
      span 
        margin-right unit(10/$rem,rem)
    .shop-youhui      
      display flex
      align-items  center
      justify-content space-between
      width unit(320/$rem,rem)
      font-size unit(14/$rem,rem)
      border #cccccc solid unit(1/$rem,rem)
      padding unit(4/$rem,rem)
      .pinpai
        font-size unit(12/$rem,rem)
        font-weight bold
        background  #4caf50
        margin-right unit(5/$rem,rem)
        padding unit(2/$rem,rem)
        color #fff
      .title
        font-weight bold
        width unit(230/$rem,rem)
        overflow hidden
        text-overflow ellipsis
        white-space nowrap
      .youhui-num
        display flex
        align-items center
        justify-content center  
        .xsj-bottom
          border-left unit(6/$rem,rem) solid transparent
          border-top unit(6/$rem,rem) solid #000
          border-bottom  unit(6/$rem,rem) solid transparent
          border-right unit(6/$rem,rem) solid transparent
          height 0
          width 0
          margin-top unit(5/$rem,rem)
          margin-left unit(5/$rem,rem)
</style>



